.registerParent {
  height: 100vh;
  width: 100vw;
}

/* .ant-select-selection__rendered {
  background-color: red !important;
} */
.ant-select-arrow {
  color: white !important;
}

.selectStyling {
  width: 90%;
  border: 1px solid white;
  border-radius: 4px;
}
.ant-select-selection {
  border: none !important;
}

.ant-select-selection, .ant-select-selection--single {
  background-color: transparent !important;
}

.ant-select-selection:hover {
  outline: 0 !important;
}

.ant-select-selection--single:hover {
  outline: 0 !important;
}

.formText {
  margin: 0;
  color: #FCD139;
  font-size: 0.8em;
  padding-bottom: 3%;
}

.overRideSelect {
}

.cutieIconStyle {
  width: 6%;
  padding: 5px;
}

.iconStylingDrop {
  float: right;
  padding-right: 10px;
}

.overRideDiv {
  border: 1px solid white;
  height: 32px;
  border-radius: 4px;
  cursor: pointer;
  color: white !important;
}

.overRideDrop {
}

.registerInput {
  border: none;
  width: 90%;
  border-bottom: 1px solid #E0E0E0;
  background-color: transparent;
  padding-bottom: 5px;
  color: white;
}

.registerInput:focus {
  border: 2px solid white;
}

.ant-input {
  border: none !important;
  background-color: transparent !important;
  color: white !important;
}

.ant-input:focus {
  background-color: #142435 !important;
}

::placeholder {
  color: #B8B8B8;
}

.formFlex {
  display: flex;
  margin: auto;
  margin-left: 4%;
}

.firstInner {
  margin-top: 5%;
  width: 33%;
}

.secondInner {
  margin-top: 5%;
  width: 50%;
}

.secondInner2 {
  margin-top: 3%;
  width: 50%;
}

.checkInner {
  margin-top: 5%;
  width: 100%;
}

.registerText {
  color: white;
  font-size: 3em;
  font-weight: 150;
}

.leftRegister {
  width: 100%;
  height: 100vh;
  background-color: #2E3E4E;
}

.iconStyling {
  width: 30%;
}

.formContainer {
  width: 60%;
  margin: auto;
  border-radius: 8px;
  padding-bottom: 3%;
  background-color: #27384F;
  margin-bottom: 4%;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
   -webkit-transition-delay: 9999s;
   transition-delay: 9999s;
}

.cutieIcon {
  opacity: 0.15;
  position: absolute;
  top: 10%;
  left: 16%;
}

.outerParent {
  position: relative;
}

.iconFlex {
  display: flex;
  width: 100%;
}

.iconStyles {
  color: white !important;
  padding-left: 20%;
  font-size: 1.25em;
}

.antInputStyling {
  border: 1px solid white !important;
  border-radius: 4px;
}

.checkText {
  color: white;
  padding-left: 10px;
}

.ant-checkbox-checked .ant-checkbox-inner {
  background-color: #142435 !important;
}

.registrationTitle {
  color: #FCD139;
  font-weight: 300;
  margin: 0;
}

.submitInner {
  margin-top: 2%;
  display: flex;
  width: 100%;
}

.applyButton {
  border: 2px solid white !important;
  border-radius: 6px !important;
  background-color: #475768 !important;
  color: white !important;
}

.applyButton:hover {
  background-color: #909090 !important;
  border: 2px solid white;
}

.buttonStyling {
  margin: auto;
  margin-top: 2%;
}

.displayDup, .displayEmpty, .displaySuccess {
  position: absolute;
}

.displayNone {
  display: none;
}

.toastStyling {
  position: absolute;
}

@media screen and (max-width: 414px){

/* .applyButton {
  border: 2px solid red !important;
  border-radius: 6px !important;
  background-color: #475768 !important;
  color: white !important;
} */

.displayDup, .displayEmpty, .displaySuccess {
  display: none;
}

.hamburgerRegister {
  display: none;
}

.firstInner {
  margin-top: 5%;
  width: 100%;
  padding-left: 10px;
}

.secondInner {
  margin-top: 5%;
  width: 100%;
  padding-left: 10px;
}

.secondInner2 {
  margin-top: 3%;
  width: 100%;
  padding-left: 10px;
}

.formFlex {
  display: block;
  margin: auto;
  margin-left: 4%;
}
.checkText {
  color: white;
  padding-left: 10px;
  width: 80%;
}

.formContainer {
  margin: auto;
  border-radius: 8px;
  padding-bottom: 3%;
  background-color: #27384F;
  margin-bottom: 4%;
  width: 100%;
}

.registrationTitle {
  color: #FCD139;
  font-weight: 300;
  margin: 0;
  font-size: 30px;
}

.cutieIcon {
  opacity: 0.15;
  position: absolute;
  top: 15%;
  left: 17%;
  display: none;
}
}
